body {
  margin: 0;
  padding: 0;
  font-family: 'Nanum Myeongjo', serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

h1 {
  font-family: 'Quattrocento', serif;
}

a {
  color: inherit;
  text-decoration: none;
}

p {
  font-size: 1.1em;
}