@keyframes initanim {
  0% {
    transform: rotateY(-30deg);
  }
  66% {
    transform: rotateY(8deg);
  }
  100% {
    transform: rotateY(0deg);
  }
}

@keyframes blink {
  0% {
    opacity: 0.1;
  }
  50% {
    opacity: 1;
  }
  100% {
    opacity: 0.1;
  }
}

.break {
  height: 3em;
}

.hidden {
  display: none !important;
}

.App {
  .social {
    img {
      width: 1em;
      display: inline-block;
    }
    h3 {
      display: inline-block;
      margin-left: 0.3em;
      margin-top: 0;
      margin-bottom: 0;
    }
  }
  overflow-x: hidden;
  .landing-flipped {
    transform: rotateY(180deg);
    .overlay-desktop,
    .overlay-mobile {
      display: none;
    }
  }
  .toggle-lang {
    position: absolute;
    margin: 0;
    top: 0;
    cursor: pointer;
    user-select: none;
  }
  .overlay-mobile {
    display: none;
  }
  @media screen and (max-width: 600px) {
    .overlay-mobile {
      display: inherit;
      animation: blink 3s infinite;
      position: absolute;
      bottom: 10%;
      text-align: center;
      width: 100%;
      z-index: 999;
      img {
        width: 30px;
      }
      p {
        margin: 0;
      }
    }
  }
  .overlay-desktop {
    animation: blink 3s infinite;
    position: absolute;
    right: 0;
    top: 50%;
    transform: translateY(-15px);
    z-index: 999;
    div {
      display: inline-block;
    }
    img {
      height: 30px;
    }
    p {
      margin: 0;
    }
    @media screen and (max-width: 600px) {
      display: none;
    }
  }
  .landing {
    transition: transform 0.5s;
    transform-style: preserve-3d;
    animation: initanim 0.8s;
    width: 90vw;
    height: 90vh;
    margin-top: 5vh;
    margin-left: 5vw;
    border: #afafaf 1px solid;
    position: relative;
    .simple-back {
      width: 100%;
      backface-visibility: hidden;
      transform: rotateY(180deg);
      position: absolute;
      height: 100%;
      div.box:nth-child(1) {
        float: left
      }
      div.box {
        display: inline-block;
        padding: 45px;
        width: calc(50% - 90px);
        height: calc(100% - 90px);
        h2 {
          margin: 0;
        }
      }
      @media screen and (max-width: 600px) {
        div.box:nth-child(1) {
          width: calc(100% - 90px);
        }
        div.box:nth-child(2) {
          display: none;
        }
      }
    }
    .simple {
      backface-visibility: hidden;
      position: absolute;
      display: flex;
      justify-content: center;
      align-items: center;
      text-align: center;
      height: calc(100% - 90px);
      width: calc(100% - 90px);
      margin-top: 45px;
      margin-left: 45px;
      margin-right: 45px;
      border: #dfdfdf 1px solid;
      h1,
      h2 {
        margin-top: 0;
      }
      h1 {
        margin-bottom: 0;
        font-size: 3em;
      }
      h1,
      h2,
      h3.faint {
        user-select: none;
      }
      .faint {
        color: #949494;
      }
    }
    .bg {
      width: 100%;
      height: 100%;
      overflow: hidden;
      .text {
        white-space: nowrap;
        transform: translate(-4vw, -15vh) rotateZ(-10deg);
        h2 {
          margin: 0;
          margin-right: 0.3em;
          display: inline;
          font-size: 8em;
          letter-spacing: 0.05em;
        }
      }
      .text-mid {
        text-align: center;
        h2 {
          color: white;
          background: black;
          padding-left: 0.3em;
          padding-right: 0.3em;
        }
      }
      color: #f1f1f1;
      background: white;
      user-select: none;
    }
  }
}